@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --main-color: #4FE3C1;
}

html {
    height: -webkit-fill-available;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
}

body::-webkit-scrollbar{
    display: none;
}

body {
    margin: 0;
    color: #363A3F;
    font-weight: 500;
    letter-spacing: 0.3px;
    font-family: 'Manrope', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh;
    min-height: -webkit-fill-available;
    background: linear-gradient(45deg, #eaeaea, transparent);
}

#root {
    min-height: 100vh;
    min-height: -webkit-fill-available;
}

.app {
    width: 100vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.screen {
    width: 100%;
    height: 100%;
    background: #FFFFFF;
}

.bg-main {
    background-color: var(--main-color);
}

.bg-grey {
    background-color: #F4F4F5;
}

.bg-grey-two {
    background-color: #F4F4F5;
}

.bg-grey-three {
    background-color: #E9E9EB;
}

.bg-black {
    background-color: #17191C;
}

.bg-dark-grey {
    background-color: #282B2E;
}

.bg-dark-grey:disabled {
    background-color: #D3D3D8;
}

.bg-red {
    background-color: #FFE2E9;
}

.bg-green {
    background-color: #D8F5EF;
}

.text-grey-two {
    color: #91919D;
}

.border-grey {
    border: 1px solid #91919D;
}

.text-grey {
    color: #7a7a90;
}

.text-grey-three {
    color: #282B2E;
}

.text-primary {
    color: #4FE3C1;
}

.disappear {
    animation: disappear 0.25s forwards;
    animation-delay: 1.25s;
}

@keyframes disappear {
    from { opacity: 1 }
    to { opacity: 0 }
}

.title-bar {
    height: 64px;
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 49;
}

pre {
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

.custom-scollbar::-webkit-scrollbar {
    width: 0.5em;
}

.custom-scollbar::-webkit-scrollbar-track {
    background-color: rgba(255,255,255,0.2);
}

.custom-scollbar::-webkit-scrollbar-thumb {
    background-color: white;
    outline: none;
}

.menu .menu__item {
    background: white;
    border-bottom: 1px solid #f3f3f3;
}

.menu .menu__item:last-child {
    border-bottom: none;
}

.fs-15 {
    font-size: 15px;
}

.text-2\.5xl {
    font-size: 27px;
}

.input {
    border-width: 2px;
    border-radius: 5px;
}

.input {
    border-width: 2px;
    border-radius: 5px;
}

.input-active:focus {
    border-color: #4FE3C1;
}

.input-white {
    background-color: white;
    border-color: white;
}

.tooltip {
    position: relative;
    border: 2px solid #585F63;
    border-radius: 4px;
    padding: 15px;
    background: white;
    color: #011831;
    margin-top: -4px;
}

.tooltip-hook {
    position: absolute;
    z-index: 40;
}

.tooltip-hook::before {
    content: "";
    position: absolute;
    top: -7px;
    left: 50%;
    margin-left: -6px;
    border-width: 14px;
    border-style: solid;
    border-color: transparent transparent #585F63 transparent;
}

.tooltip-hook::after {
    content: "";
    position: absolute;
    top: -7px;
    left: 50%;
    margin-left: -9px;
    border-width: 17px;
    border-style: solid;
    border-color: transparent transparent white transparent;
}

.checkbox {
    width: 17px;
    height: 17px;
    min-width: 17px;
    appearance: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    cursor: pointer;
    background-color: #282B2E;
}

.checkbox:checked:after {
    content: '';
    width: 11px;
    height: 14px;
    left: 0;
    color: black;
    display: block;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='22px' height='17px' viewBox='0 0 22 17' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg id='Page-1' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd' stroke-linecap='round' stroke-linejoin='round'%3E%3Cpolyline id='Path' stroke='white' stroke-width='4' points='2.38964844 9.09033203 7.93798828 14.4946289 19.6391602 2.5859375'%3E%3C/polyline%3E%3C/g%3E%3C/svg%3E");
}

.item-enter {
    opacity: 0;
}

.item-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.item-exit {
    opacity: 1;
}

.item-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
}
